<template>
  <div class="login">
    <h1>书屋 | 城市书屋管理系统</h1>
    <transition name="fade">
      <a-form
        :form="form"
        :label-col="{ span: 5 }" 
        :wrapper-col="{ span: 15 }"
        class="form"
        @submit="handleSubmit"
        ref="form"
        v-show="reflesh"
      >
        <a-form-item
          :wrapper-col="{ span: 24 }"
          class="title"
        >
          <div>{{ mode === 'login' ? '登录' : '注册账号' }}</div>
        </a-form-item>
        <!-- <a-form-item :wrapper-col="{ offset: 5, span: 15 }" style="margin-bottom: 4px">
          <div
            class="tip_text" 
            @click="toRegister" 
            v-if="mode === 'login'"
          >
            还没有账号？点击注册
          </div>
          <div 
            class="tip_text" 
            @click="toLogin"
            v-else
          >
            已有账号？马上登录
          </div>
        </a-form-item> -->

        <a-form-item
          label="用户名"
        >
          <a-input
            v-decorator="['account', rules.username]"
          ></a-input>
        </a-form-item>

        <a-form-item
          label="密码"
        >
          <a-input 
            type="password"
            v-decorator="['password', rules.password]"
          ></a-input>
        </a-form-item>

        <a-form-item
          label="确认密码"
          v-if="mode === 'register'"
        >
          <a-input 
            type="password"
            v-decorator="['confirmPassword', rules.confirmPassword]"
          ></a-input>
        </a-form-item>

        <a-form-item
          label="验证码"
          v-if="mode === 'register'"
        >
          <a-row type="flex">
            <a-col :span="15">
              <a-input
                v-decorator="['code', rules.code]"
              ></a-input>
            </a-col>
            <a-col :span="8" :offset="1">
              <img src="" alt="">
            </a-col>
          </a-row>
        </a-form-item>

        <a-form-item :wrapper-col="{offset: 5, span: 15}">
          <a-button type="primary" block html-type="submit">{{ mode === 'login' ? '登录' : '注册' }}</a-button>
        </a-form-item>

        <a-form-item  :wrapper-col="{ offset: 5, span: 15 }" style="margin-bottom: 0;">
          <div class="tip_text" @click="resetPasswordVisble = true" v-if="mode === 'login'">重置密码</div>
        </a-form-item>
      </a-form>
    </transition>

    <a-modal
      v-model="resetPasswordVisble"
      title="重置密码"
      okText="重置"
      cancelText="取消"
      centered
      @ok="handleResetPassword"
      @cancel="cancelResetPassword"
    >
      <a-form
        :form="resetPasswordForm"
        :label-col="{ span: 5 }"
        :wrapper-col="{ span: 16 }"
        @submit="resetPassword"
        ref="resetPasswordForm"
      >
        <a-form-item label="原密码">
          <a-input
            type="password"
            v-decorator="['oldPassword', rules.oldPassword]"
          ></a-input>
        </a-form-item>
        <a-form-item label="新密码">
          <a-input
            type="password"
            v-decorator="['newPassword', rules.newPassword]"
          ></a-input>
        </a-form-item>
        <a-form-item label="确认新密码">
          <a-input
            type="password"
            v-decorator="['confirmNewPassword', rules.confirmPassword]"
          ></a-input>
        </a-form-item>
      </a-form>
    </a-modal>

  </div>
</template>
<script>
import { Button, Form, Row, Col, Modal } from 'ant-design-vue'
import { mapActions } from 'vuex'
export default {
  name: 'Login',
  components: { AForm: Form, AFormItem: Form.Item, ARow: Row, ACol: Col, AModal: Modal },
  data() {
    const rules = {
      username: {
        rules: [ {required: true, message: '请输入用户名'} ]
      },
      password: {
        rules: [
          { required: true, message: '请输入密码' },
          { validator: this.validatePassword }
        ]
      },
      code: {
        rules: [{ required: true, message: '请输入验证码' }]
      },
      confirmPassword: {
        rules: [
          { required: true, message: '请重新输入一次密码'},
          { validator: this.validateConfirmPassword }
        ]
      },
      oldPassword: {
        rules: [ { required: true, message: '请输入原密码' } ]
      },
      newPassword: {
        rules: [
          { required: true, message: '请输入新密码' },
          { validator: this.validatePassword }
        ]
      }
    }
    
    return {
      mode: 'login',
      reflesh: true,
      resetPasswordVisble: false,
      form: this.$form.createForm(this, { name: 'login' }),
      resetPasswordForm: this.$form.createForm(this, { name: 'resetPassword'}),
      rules
    }
  },
  methods: {
    ...mapActions('login', {
      login: 'login'
    }),
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          this.login(values).then(res => {
            if (!res.id) {
              this.$message.error('用户名或密码错误')
              return ;
            }
            this.$message.success('登录成功')
            sessionStorage.setItem('is_login', true)
            sessionStorage.setItem('user_id', res.id)
            this.$router.push({
              path: '/'
            })
          })
        }
      });
    },
    resetForm() {
      this.form.resetFields()
    },
    animation(mode) {
      this.resetForm()
      this.reflesh = false
      let timer = null
      timer = setTimeout(() => {
        this.mode = mode
        this.reflesh = true
        clearInterval(timer)
        timer = null
      }, 300)
    },
    toRegister() {
      this.animation('register')
    },
    toLogin() {
      this.animation('login')
    },
    handleResetPassword(e) {
      e.preventDefault()
      this.resetPasswordForm.validateFields((err, values) => {
        if (!err) {
          console.log(values)
        }
      })
    },
    resetPassword() {
      this.handleResetPassword()
    },
    cancelResetPassword() {
      this.resetPasswordForm.resetFields()
      this.resetPasswordVisble = false
    },
    validatePassword(rule, value, callback) {
      if (!this.resetPasswordVisble && this.mode === 'login') {
        callback()
        return ;
      }
      const reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/
      if (value && value.length < 8 || value && value.length > 16) {
        callback('密码长度需要在8~16位之间')
      }
      if (value && !value.match(reg)) {
        callback('密码需要包含英文和数字')
      } else {
        callback()
      }
    },
    validateConfirmPassword(rule, value, callback) {
      if ( value && value !== this.form.getFieldValue('password')) {
        callback('两次输入密码不一致')
      } else {
        callback()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.login {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(to right, #a7a6cb 0%, #8989ba 70%, #8989ba 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .form {
    width: 600px;
    height: 450px;
    background: rgba(255, 255, 255, .4);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .ant-row {
      width: 100%;
    }
    .tip_text {
      text-align: right;
      color: #5da9ff;
      cursor: pointer;
    }
    .title {
      font-size: 2em;
      color: #000;
      font-weight: 600;
      text-align: center;
      margin-bottom: 8px;
    }
  }
}
.fade-enter-active, .fade-leave-active {
  transition: all .3s ease;
}
.fade-enter-to {
  transform: scale(1);
}
.fade-leave-to {
  transform: scale(0);
}
</style>